@import url(https://fonts.googleapis.com/css?family=Comfortaa:300,400,500,600,700&display=swap&subset=cyrillic,cyrillic-ext,latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Comfortaa", cursive; }

html,
body {
  background-color: #ebe8db;
  height: 100%; }

#root,
.app {
  height: 100%; }

.app {
  display: flex; }

.container {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  padding: 1rem; }

.actions button {
  font-size: 1rem;
  padding: .5rem;
  cursor: pointer; }

.textarea {
  width: 100%;
  height: 100%;
  display: block; }

